import { Component } from "react";

import './activities.css';

export default class Activities extends Component {
            
    render() {
        return(
            <div className="activities">
                <div className="page-content">
                    <header>
                        <h1 className="text-color-green">Aktivitten und Veranstaltungen</h1>
                    </header>
                    <article>
                        <span>Im Lauf des Jahres sind wir nicht nur im Schiesport aktiv. Viele weitere Termine und Aktivitten sind bei uns fest im kalender verankert. Die genauen Termine kannst du auf unserem Kalender finden.</span>
                        <h2 className="text-color-blue">Hier eine Auswahl unserer Aktivitten</h2>
                        <dl>
                            <dt>
                                <span className="bold-text">
                                    Schiebude
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Bei Mai- und Sommerfesten in der Gemeinde sind wir mit unserer eigenen Schiebude vertreten.
                                </span>
                            </dd>

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Glassammlung
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Zwei mal im Jahr sammeln wir in der Gemeinde Horgenzell Altglas.
                                </span>
                            </dd>

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Gemeindemeisterschaft
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Bei der jhrlichen Gemeindemeisterschaft treten rtliche Vereine und Gruppierungen gegeneinander an.
                                    Es warten attraktive Preise und die Verkndung des Neuen Gemeindemeisters.
                                </span>
                            </dd>

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Jedermannsschieen
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Beim gemtlichen Feierabendhock mit Jedermannsschieen knnen kleine Preise geschossen werden.
                                </span>
                            </dd>

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Knigsschieen mit Preisschieen
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Jhrlich im Oktober findet unser Knigsschieen mit Preisschieen statt. Die Knige und Kniginnen drfen den Verein mit den bergebenen Ketten ein Jahr lang representieren.
                                </span>
                            </dd>                            

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Schtzenball
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Am Fasnetssontag ist bei uns traditionell der Schtzenball. Jung und Alt feiern hier bis in die Morgenstunden.
                                </span>
                            </dd>                            

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Cross-Biathlon
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Der Schtzenkreis Ravensburg veranstaltet seit vielen Jahren bei uns am Schtzenhaus den Cross-Biathlon.
                                    Viele Vereine aus dem eigenen und benachbarten Kreisen nehmen an dem Event teil.
                                </span>
                            </dd>                            

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    Weihnachtsfeier
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Zum Abschluss des Jahres feiern wir in gemtlicher Runde und gutem Essen in unserem Vereinsheim.
                                </span>
                            </dd>                            

                            <br></br>
                            <dt>
                                <span className="bold-text">
                                    ffentliche Auftritte
                                </span>                                
                            </dt>                            
                            <dd>
                                <span>
                                    Zu Fronleichnahm, Hochzeiten und anderen ffentlichen Veranstaltungen rckt der Verein mit Fahne und Uniform aus.
                                </span>
                            </dd>
                        </dl>
                    </article>
                </div>                
            </div>
        );
    }
}